import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from 'mdbreact';
import * as actions from '../../store/actions/index';

class RegisterPage extends Component {
  state = {
    username: '',
    password: '',
    passwordConfirmation: '',
    isSignup: true
  };

  componentDidMount() {
    if (this.props.authRedirectPath !== '/dashboard') {
        this.props.onSetAuthRedirectPath();
    }
  };

  submitHandler = event => {
    event.preventDefault();
    const { username, password, isSignup } = this.state;
    this.props.onAuth( username, password, isSignup );
  };
    
  changeHandler = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  render() {
    let authRedirect = null;
    if (this.props.isAuthenticated) {
      authRedirect = <Redirect to={this.props.authRedirectPath}/>
    }
      return (
        <MDBContainer>
          <MDBRow className="d-flex justify-content-center">
            <MDBCol md="6">
                <form onSubmit={this.submitHandler}>
                  <p className="h5 text-center mb-4">Register</p>
                  <div className="grey-text">
                    <MDBInput
                      className="form-control"
                      type="text"
                      name="username"
                      id="username"
                      value={this.state.username}
                      onChange={this.changeHandler}
                      label="Your username"
                      icon="user"
                      required
                      /*{pattern="[a-z\d](?:[a-z\d]|-(?=[a-z\d])){5,15}"}*/
                    />
                    <MDBInput
                      className="form-control"
                      type="password"
                      name="password"
                      id="password"
                      value={this.state.password}
                      onChange={this.changeHandler}
                      label="Your password"
                      icon="lock"
                      required
                      /*{pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{6}$"}*/
                    />
                    <MDBInput
                      className="form-control"
                      type="password"
                      name="passwordConfirmation"
                      id="passwordConfirmation"
                      value={this.state.passwordConfirmation}
                      onChange={this.changeHandler}
                      label="Password confirmation"
                      icon="lock"
                      required
                      /*{pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{6}$"}*/
                    />
                </div>
                <div className="text-center">
                  <MDBBtn type="submit">Register</MDBBtn>
                </div>
                </form>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      );
    }
}

const mapStateToProps = state => {
  return {
    error: state.error,
    isAuthenticated: state.isAuthenticated,
    authRedirectPath: state.authRedirectPath
  };
};

const mapDispatchToProps = dispatch => {
  return {
      onAuth: (username, password, isSignup) => dispatch(actions.auth(username, password, isSignup)),
      onSetAuthRedirectPath: () => dispatch(actions.setAuthRedirectPath('/dashboard'))
    };
};

export default connect( mapStateToProps, mapDispatchToProps )( RegisterPage );
